<template>
  <div class="relative">
    <button type="button" @click="toggle" class="block focus:outline-none" @focus="buttonHasFocus = true" @blur="buttonHasFocus = false">
      <slot name="trigger" :buttonHasFocus="buttonHasFocus" :isOpen="isOpen"></slot>
    </button>
    <div :class="[ isOpen ? 'block' : 'hidden' ]">
      <button type="button" @click="isOpen = false" class="z-20 block fixed opacity-1 inset-0 w-full h-full cursor-default focus:outline-none"></button>
      <div class="absolute right-0 z-40">
        <slot name="dropdown"></slot>
      </div>
    </div>   
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false,
      buttonHasFocus: false,
    }
  },
  methods: {
    toggle() {
      this.isOpen = !this.isOpen
    }
  },
  mounted() {
    const onEscape = (e) => {
      if (!this.isOpen || e.key !== 'Escape') {
        return
      }
      console.log('closing')
      this.isOpen = false
    }
    document.addEventListener('keydown', onEscape)
    this.$on('hook:destroyed', () => {
      document.removeEventListener('keydown', onEscape)
    })
  }
}
</script>
