<template>
  <div>
    <div class="pb-5/6 relative">
      <img :src="property.imageURL" alt="" class="h-64 w-full h-full absolute inset-0 object-cover object-center rounded-lg shadow-md">
    </div>
    <div class="px-3 -mt-16 relative">
      <div class="px-4 py-4 bg-white rounded-lg shadow-lg">
        <div class="flex items-baseline">
          <CardBadge>Plus</CardBadge>
          <div class="ml-2 text-xs text-gray-600 uppercase tracking-wide font-semibold">{{ property.bed }} {{ property.bed === 1 ? 'bed' : 'beds' }} &bull; {{ property.bath }} {{ property.bath === 1 ? 'bath' : 'baths' }}</div>
        </div>
        <h4 class="mt-1 font-semibold text-lg">{{ property.title }}</h4>
        <div class="mt-1">
          <span>{{ formattedPrice }}</span>
          <span class="ml-1 text-gray-600 text-sm"> / wk</span>
        </div>
        <div class="mt-2 flex items-center text-sm text-gray-600">
          <svg v-for="index in 5" :key="index" class="h-4 w-4 fill-current" :class="property.rating >= index ? 'text-teal-500' : 'text-gray-400'" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M8.133 20.82c-1.147.593-2.488-.365-2.269-1.622l.739-4.239-3.13-3.002c-.927-.89-.415-2.441.867-2.624l4.324-.619 1.934-3.856c.573-1.144 2.23-1.144 2.804 0l1.934 3.856 4.324.619c1.282.183 1.794 1.734.866 2.624l-3.129 3.002.739 4.239c.219 1.257-1.122 2.215-2.269 1.622L12 18.819l-3.867 2z"/></svg>
          <span class="ml-2">{{ property.reviewCount }} {{ property.reviewCount === 1 ? 'review' : 'reviews' }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    CardBadge: {
      render(h) {
          return h('span', {
            class: 'px-2 py-1 inline-block bg-teal-200 text-teal-800 leading-none tracking-wide uppercase text-xs font-semibold rounded-full'
          }, this.$slots.default)
      }
    }
  },
  props: [
    'property'
  ],
  computed: {
    formattedPrice() {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      });
      return formatter.format(this.property.price / 100)
    }
  }
}
</script>
